<template>
  <nav class="fixed w-full flex flex-wrap items-center justify-between px-2 py-1 navbar-expand-lg bg-opacity-90 -mb-24 z-20" id="nav" :class="{sticky:active}">
    <div class="container px-4 mx-auto flex flex-wrap items-center justify-between">
      <div class="w-full relative flex justify-between lg:w-auto  px-4 lg:static lg:block lg:justify-start">
        <router-link to="/" class="text-3xl font-bold leading-relaxed inline-block mr-4 py-2 whitespace-no-wrap uppercase text-white opacity-100" href="#pablo" >
          <span class="text-blue-500">US</span>HR LLC
        </router-link>
        <button class="text-white cursor-pointer text-2xl leading-none  py-1 border border-solid border-transparent rounded bg-transparent block lg:hidden outline-none focus:outline-none" type="button" v-on:click="toggleNavbar()">
          <i class="fas fa-bars"></i>
        </button>
      </div>
      <div v-bind:class="{'hidden': !showMenu, 'flex': showMenu}" class="lg:flex lg:flex-grow items-center ">
        <ul class="flex flex-col lg:flex-row list-none ml-auto">
          <li class="nav-item ml-2">
            <a class="px-3 py-2 flex items-center text-xl uppercase font-bold leading-snug text-white hover:opacity-75" href="tel:+707-701-0707">
              <span class="text-blue-500 pr-2">Call now!</span>707-701-0707
            </a>
          </li>
          <li class="nav-item ml-2">
            <a class="px-3 py-2 flex items-center text-xl uppercase font-bold leading-snug text-white hover:opacity-75" href="#pablo">
              About us
            </a>
          </li>
          <li class="nav-item ml-2">
            <a class="px-3 py-2 flex items-center text-xl uppercase font-bold leading-snug text-white hover:opacity-75" href="#pablo">
              Contact
            </a>
          </li>
          <li class="nav-item ml-2">
            <router-link to="/login" class="px-3 py-2 flex items-center text-xl uppercase font-bold leading-snug text-white hover:opacity-75" href="#pablo">
              Sign up
            </router-link>
          </li>
        </ul>
      </div>
    </div>
  </nav>
</template>

<script>
export default {
  name: "pink-navbar",
  data() {
    return {
      showMenu: false,
      active: false
    }
  },
  mounted(){
  window.document.onscroll = () => {
      let navBar = document.getElementById('nav');
      if(window.scrollY > navBar.offsetTop){
        this.active = true;
        } else {
        this.active = false;
      }
    }
  },
  methods: {
    toggleNavbar: function(){
      this.showMenu = !this.showMenu;
    },
    toggleNavClass(){
        if(this.active == false){
          return 'nav'
        } else {
          return 'sticky-nav'
        }

    }
  }
}
</script>
<style scoped>
  .nav {
    transition: 200ms;
    padding: 25px;
  }

  .sticky-nav{
    transition: 200ms;
    padding: 20px;
  }

  #nav {
    display: flex;
    justify-content: space-between;
    width: 100%;
    position: fixed;
    top: 0;
  }

  #nav.sticky{
    transition: 150ms;
    background-color: #002482;
  }
</style>